//
<template>
    <div>
        <div id="login_one">
            <img src="./imgs/back.png" alt="" @click="ret()">
            <span></span>
        </div>
        <div id="login_two">
            <input v-model="username" type="text" placeholder=""><br>
        </div>
        <div id="login_three">
             <input v-model="password" type="text" placeholder=""><br>
        </div>
        <div id="login_four">
            <!-- <input v-model="codeNumber" type="text" placeholder="">
             <img :src="code" alt="">

            <button @click="getCode()"></button>
            <br> -->
            <input v-model="codeNumber" type="text" placeholder="">
            <img :src="code" alt="">
            <div>
                <p></p>
                <p @click="getCode()"></p>
            </div>
            <br>
        </div>
        <div id="login_five">
            <p></p>
            <p></p>
        </div>
        <div id="login_six">
            <button @click="login()"></button>
        </div>
        <div id="login_seven">
            <router-link to="/resetmima" id="reset"></router-link>
        </div>
    </div>
</template>
<script>
import { Loading } from 'element-ui';
let loadingInstance;
export default {
    name:"login",
    data(){
        return{
            code:"",//base64
            username:"",
            password:"",
            codeNumber:""//
        }
    },
    created(){
        //
        loadingInstance = Loading.service({
            fullscreen: true,
            text:'...',
            background:"grey",
            spinner:"el-icon-loading"
        });
        //
        this.getCode();
    },
    methods:{
        ret(){
            this.$router.go(-1);
        },
        getCode(){
            const api = "https://elm.cangdu.org/v1/captchas";
            this.$http({
                url:api,
                method:"post",
                //cookies,
                withCredentials:true
            }).then(res=>{
                console.log("");
                console.log(res);
                this.code = res.data.code;
            })
        },
        //created
        login(){
            const api = "https://elm.cangdu.org/v2/login";
            this.$http({
                url:api,
                method:"post",
                withCredentials:true,
                data:{
                    username:this.username,
                    password:this.password,
                    captcha_code:this.codeNumber
                }
            }).then(res=>{
                console.log("login");
                console.log(res);
                if(res.data.message){
                    alert(res.data.message);//
                }
                else{
                    // 
                    this.$store.commit("getInp",res.data);
                    // 
                    this.$store.commit("getname",res.data.username);
                    console.log(""+res.data.username);
                    //queryparams
                    //queryparams
                    this.$router.push({
                        name:"citytwo",
                        
                        // query:res.data,
                        // params:{pID:res.data.id}
                    });
                }
            })
        }
    }
}
</script>
<style lang="">
    #login_one{
        background-color: #3091e9;
        color: white;
        font-size: 0.16rem;
        height: 0.5rem;
        line-height: 0.5rem;
        position: relative;
    }
    #login_one img{
        position: absolute;
        top: 0.12rem;
        left: 0.12rem;
        height:0.3rem;
    }
     #login_one span{
         font-size: 0.16rem;
         color: white;
         position: absolute;
         left: 40%
     }
     #login_two {
         font-size: 0.12rem;
         height: 0.5rem;
         line-height: 0.5rem;
         background-color: #fff;
         border-bottom: 0.5px solid rgb(204, 204, 204);
     }
     #login_two input{
         margin-left: 0.12rem;
     }
     #login_three{
         font-size: 0.12rem;
         height: 0.5rem;
         line-height: 0.5rem;
         background-color: #fff;
         border-bottom: 0.5px solid rgb(204, 204, 204);
     }
     #login_three input{
         margin-left: 0.12rem;
     }
      #login_four{
        font-size: 0.12rem;
         height: 0.5rem;
         /* line-height: 0.5rem; */
         background-color: #fff;
         border-bottom: 0.5px solid rgb(204, 204, 204);
         position: relative;
     }
     #login_four input{
         position: absolute;
         top: 0.12rem;
         left: 0.12rem;
     }
     #login_four img{
         position: absolute;
         right: 1rem; 
         top: 0.12rem;
     }
      #login_four > div p:nth-child(1){
         position: absolute;
         top:0.12rem;
         right: 0.12rem;
     }
     #login_four > div p:nth-child(2){
         position: absolute;
         top:0.3rem;
         right: 0.12rem;
         color: #3091e9;
     }
     #login_five{
         color: red;
         height: 0.5rem;
         border-bottom: 0.5px solid rgb(204, 204, 204);
         font-size: 0.03rem;
     }
     #login_five p{
         margin-left: 0.08rem;
         padding-top: 0.08rem;
     }
     #login_six{
         
         background-color: green;
         text-align: center;
         height: 0.5rem;
         line-height: 0.5rem;
         font-size: 0.03rem; 
     }
     #login_six button{
         background-color: green;
         color: white;
         border: none;
     }
     #login_seven{
        height: 0.5rem;
         line-height: 0.5rem;
         position: relative;
     }
     #reset{
         color: #3091e9;
         font-size: 0.12rem;
         position: absolute;
         right:0%;
     }
    
</style>


