//
<template>
    <div>
        <div id="resetmima_one">
            <img src="./imgs/back.png" alt="" @click="ret()">
            <span></span>
        </div>
        <div id="resetmima_two">
            <input v-model="username" type="text" placeholder=""><br>
        </div>
        <div id="resetmima_three">
             <input v-model="oldpassWord" type="text" placeholder=""><br>
        </div>
        <div id="resetmima_four">
             <input v-model="newpassword" type="text" placeholder=""><br>
        </div>
        <div id="resetmima_five">
             <input v-model="confirmpassword" type="text" placeholder=""><br>
        </div>
        <div id="resetmima_six">
            <input v-model="captcha_code" type="text" placeholder="">
            <img :src="code" alt="">
            <div>
                <p></p>
                <p @click="getCode()"></p>
            </div>
            <br>
        </div>
        <div id="resetmima_seven">
            <button @click="resetmima()"></button>
        </div>
    </div>
</template>

<script >
export default {
    name:"login",
    data(){
        return{
            code:"",//base64
            username:"",//
            oldpassWord:"",//
            newpassword:"",//
            confirmpassword:"",//
            captcha_code:""//
        }
    },
    created(){
        //
        this.getCode();
    },
    methods:{
        ret(){
            this.$router.go(-1);
        },
        getCode(){
            const api = "https://elm.cangdu.org/v1/captchas";
            this.$http({
                url:api,
                method:"post",
                //cookies,
                withCredentials:true
            }).then(res=>{
                console.log("");
                console.log(res);
                this.code = res.data.code;
            })
        },
        //created
        resetmima(){
            const api = "https://elm.cangdu.org/v2/changepassword";
            this.$http({
                url:api,
                method:"post",
                withCredentials:true,
                data:{
                    username:this.username,
                    oldpassWord:this.oldpassWord,
                    newpassword:this.newpassword,
                    confirmpassword:this.confirmpassword,
                    captcha_code:this.captcha_code
                }
            }).then(res=>{
                console.log("");
                console.log(res);
                if(res.data.message){
                    alert(res.data.message);//
                }
                else{
                    //queryparams
                    this.$router.push({
                        name:"home",
                        // query:res.data,
                        // params:{pID:res.data.id}
                    });
                }

            });
        }
    }
}
</script>
<style lang="">
    #resetmima_one{
        background-color: #3091e9;
        color: white;
        font-size: 0.16rem;
        height: 0.5rem;
        line-height: 0.5rem;
        position: relative;
    }
    #resetmima_one img{
        position: absolute;
        top: 0.12rem;
        left: 0.12rem;
        height:0.3rem;
    }
     #resetmima_one span{
         font-size: 0.16rem;
         color: white;
         position: absolute;
         left: 40%
     }
     #resetmima_two {
         font-size: 0.12rem;
         height: 0.5rem;
         line-height: 0.5rem;
         background-color: #fff;
         border-bottom: 0.5px solid rgb(204, 204, 204);
     }
     #resetmima_two input{
         margin-left: 0.12rem;
     }
     #resetmima_three{
         font-size: 0.12rem;
         height: 0.5rem;
         line-height: 0.5rem;
         background-color: #fff;
         border-bottom: 0.5px solid rgb(204, 204, 204);
     }
     #resetmima_three input{
         margin-left: 0.12rem;
     }
     #resetmima_four{
         font-size: 0.12rem;
         height: 0.5rem;
         line-height: 0.5rem;
         background-color: #fff;
         border-bottom: 0.5px solid rgb(204, 204, 204);
     }
     #resetmima_four input{
         margin-left: 0.12rem;
     }
     #resetmima_five{
         font-size: 0.12rem;
         height: 0.5rem;
         line-height: 0.5rem;
         background-color: #fff;
         border-bottom: 0.5px solid rgb(204, 204, 204);
         position: relative;
     }
     #resetmima_five input{
         margin-left: 0.12rem;
         position: absolute;
         top: 0.12rem;
     }
      #resetmima_six{
        font-size: 0.12rem;
         height: 0.5rem;
         /* line-height: 0.5rem; */
         background-color: #fff;
         border-bottom: 0.5px solid rgb(204, 204, 204);
         position: relative;
     }
     #resetmima_six input{
         position: absolute;
         top: 0.12rem;
         left: 0.12rem;
     }
     #resetmima_six img{
         position: absolute;
         right: 1rem; 
         top: 0.12rem;
     }
     #resetmima_six > div p:nth-child(1){
         position: absolute;
         top:0.12rem;
         right: 0.12rem;
     }
    #resetmima_six > div p:nth-child(2){
         position: absolute;
         top:0.3rem;
         right: 0.12rem;
         color: #3091e9;
     }
     #resetmima_seven{
         background-color: green;
         text-align: center;
         height: 0.5rem;
         line-height: 0.5rem;
         font-size: 0.03rem; 
         border-radius:0.05rem; 
     }
     #resetmima_seven button{
         width: 80%;
         background-color: green;
         color: white;
         border: none;
     }
    
    
</style>


