<template>
    <div>
        <div id="safeshow_head">
            <img src="./imgs/back.png" alt="" @click="ret()">
            <span></span>
        </div>
        <!--  -->
        <div id="safeshow_one">
            <div>
                <p></p>
            </div>
            <p><img src="./imgs/lian.png" alt=""></p>
            <div id="cha">
                <p id="one">
                    <span>:</span>
                    <span></span>
                </p>
                <p id="two"></p>
            </div>
        </div>
        <!--  -->
        <div id="safeshow_two"> 
            <div><p></p></div>
            <p class="toole"></p>
            <p class="toole"></p>
            <p class="toole"></p>
            <p class="toole"></p>
            <p class="toole"></p>
            <p class="toole"></p>
            <p class="toole"></p>
        </div>
         <!--  -->
        <div id="safeshow_three"> 
            <div><p></p></div>
            <p class="toole"></p>
            <p class="toole"></p>
            <p class="toole"></p>
            <p class="toole"></p>
            <p class="toole"></p>
        </div>
        <!--  -->
        <div id="safeshow_four">
            <div><p></p></div>
           <div>
                <img src="" alt="">
                <img src="" alt="">
           </div>
        </div>
    </div>
</template>
<script>
export default {
    name:"safeshow",
    methods:{
        ret(){
            this.$router.back();
        }
    }
}
</script>
<style scoped>
    #safeshow_head{
         width: 3.75rem;
        height: 0.4rem;
        line-height: 0.4rem;
        background-color: #3191e8;
        position: fixed;
        top: 0rem;
        left: 0rem;
        z-index: 2;
    }
    #safeshow_head > img{
        height: 0.15rem;
        margin-left: 0.2rem;
    }
    #safeshow_head >span{
        font-size: 0.2rem;
        color: white;
        margin-left: 1rem;
    }
    #safeshow_one{
        height: 1.4rem;
        width: 3.75rem;
        background-color: #fff;
        position: absolute;
        top: 0.4rem;
        left: 0rem; 
    }
    #safeshow_one > div:nth-child(1){
        border-bottom: 0.01rem solid #6b6b6b;
    }
    #safeshow_one > div> p{
        height: 0.4rem;
        line-height: 0.4rem;
        margin-left: 0.2rem;
    }
    #safeshow_one > p{
         float: left;
    }
    #safeshow_one > p>img{
        height: 0.8rem;
        margin-left: 0.2rem;
    }
    #cha{
        float: left;
        margin-left: 0.2rem;
        color: #6b6b6b;
    }
    #one > span:nth-child(2){
        color: red;   
    }
    #safeshow_two{
        width: 3.75rem;
        height: 2.4rem;
        background-color: #fff;
        position: absolute;
        top: 1.9rem;
        left: 0rem;
    }
    #safeshow_two > div{
        border-bottom: 0.01rem solid #6b6b6b;
    }
    #safeshow_two > div > p{
        height: 0.4rem;
        line-height: 0.4rem;
        margin-left: 0.2rem;
    }
    .toole{
        color: #6b6b6b;
        margin-left: 0.2rem;
        margin-top: 0.1rem;
    }
     #safeshow_three{
        width: 3.75rem;
        height: 2.4rem;
        background-color: #fff;
        position: absolute;
        top: 4.4rem;
        left: 0rem;
    }
    #safeshow_three > div{
        border-bottom: 0.01rem solid #6b6b6b;
    }
    #safeshow_three > div > p{
        height: 0.4rem;
        line-height: 0.4rem;
        margin-left: 0.2rem;
    }
     #safeshow_four{
        width: 3.75rem;
        height: 2.4rem;
        background-color: #fff;
        position: absolute;
        top: 6.9rem;
        left: 0rem;
    }
    #safeshow_four > div:nth-child(1){
        border-bottom: 0.01rem solid #6b6b6b;
    }
    #safeshow_four > div:nth-child(1) > p{
        height: 0.4rem;
        line-height: 0.4rem;
        margin-left: 0.2rem;
    }
</style>



